.search-container {
	text-align: left;
	z-index: 1;
	position: relative;
	max-width: 100%;
}
.search-container.hidden {
	display: none;
}
.search-container #search-box {
	font-family: inherit;
	box-sizing: border-box;
	border-radius: 3px;
	border: 1px solid rgba(0, 0, 0, 0.2);
	padding-left: 0.25rem;
	margin: 0 0 5px 0;
	width: 100%;
}

.search-container .search-list {
	display: none;
	margin-top: 1px;
	min-width: 100%;
	max-width: calc(100% + 2rem);
	max-height: 50vh;
	overflow: auto;
	position: absolute;
}
.search-container.active .search-list {
	display: block;
}
.search-container .search-list .search-item {
	height: auto;
	padding: 0.5rem;
	cursor: pointer;
	position: relative;
}

.search-container .search-list[data-pale-background="true"] .search-item:hover {
	outline: 2px solid white;
	outline-offset: -2px;
	outline-color: rgba(0, 0, 0, 0.65);
	background-color: rgba(0, 0, 0, 0.05);
}

.search-container
	.search-list[data-pale-background="false"]
	.search-item:hover {
	outline: 2px solid white;
	outline-offset: -2px;
	outline-color: rgba(255, 255, 255, 0.65);
	background-color: rgba(255, 255, 255, 0.05);
}

.search-container
	.search-list[data-pale-background="true"]
	.search-item.selected,
.search-container
	.search-list[data-pale-background="true"]
	.search-item:focus-visible {
	outline: 2px solid black;
	outline-offset: -2px;
}

.search-container
	.search-list[data-pale-background="false"]
	.search-item.selected,
.search-container
	.search-list[data-pale-background="false"]
	.search-item:focus-visible {
	outline: 2px solid white;
	outline-offset: -2px;
}

.search-container .search-list .search-item p {
	margin: 0;
	display: block;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
	pointer-events: none;
}
